/* rubik-regular - latin_cyrillic */

@font-face {
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/rubik-v12-latin_cyrillic-regular.eot');
    /* IE9 Compat Modes */
    src: local(''), url('../fonts/rubik-v12-latin_cyrillic-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('../fonts/rubik-v12-latin_cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
    url('../fonts/rubik-v12-latin_cyrillic-regular.woff') format('woff'), /* Modern Browsers */
    url('../fonts/rubik-v12-latin_cyrillic-regular.ttf') format('truetype'), /* Safari, Android, iOS */
    url('../fonts/rubik-v12-latin_cyrillic-regular.svg#Rubik') format('svg');
    /* Legacy iOS */
}


